<template>
    <div class="socks-configure">
        <div class="main-content__title">Socks</div>
        <el-form label-width="100px">
            <el-form-item label="Socks">
                <textarea class="socks-editor" :placeholder="socksProxyExample"
                          v-model="$dc.profile.socksProxyNeedParseIp"></textarea>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="saveFile"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import profileApi from '../../../api/profile';

    export default {
        name: 'configure',
        computed: {
            socksProxyExample() {
                return `#
0.0.0.0    # 80 443socks5
4.4.4.4    # 0.0.0.0socks54.4.4.480443`
            }
        },
        methods: {
            async saveFile() {
                let response = await profileApi.saveFile(this.$dc.profile);
                let serverData = response.data;
                if (serverData.code == 0) {
                    this.$message({
                        showClose: true,
                        type: 'success',
                        message: '!'
                    });
                } else {
                    this.$message.error(`${serverData.msg}`);
                }
            }
        }
    };
</script>

<style>
    .socks-configure {
        max-width: 500px;
        .el-button {
            margin-top: 15px;
            padding: 10px 28px;
            letter-spacing: 2px;
        }

        .socks-editor {
            height: 500px;
            width: 760px;
            font-size: 18px;
            padding: 10px 20px;
        }
    }

</style>



